import { Component, Host, h, Prop } from '@stencil/core';
import { joinClass } from '../../../utils/joinClass';
import i18next from 'i18next';
import { widgetState } from '../../../store';
import { joinArray } from '../../../utils/joinArray';
import { TGetProductResponse } from '../../../types/products.types';

@Component({
  tag: 'charma-card',
  styleUrl: 'charma-card.scss',
  shadow: true,
})
export class CharmaCard {
  @Prop({ reflect: true }) selected = false;
  @Prop() product: Partial<TGetProductResponse> = {};
  @Prop() isDisplayOnly = false;
  render() {
    const picture = (
      <div
        class={joinClass(
          'rounded-xl h-[100px] border-[1px] mb-[5px] overflow-hidden flex place-content-center place-items-center ',
          this.isDisplayOnly ? 'w-[142px]' : 'w-[220px]',
        )}
      >
        <img class="h-[90px]" alt="Placeholder" src={this.product?.image?.thumb} />
      </div>
    );
    const content = (
      <div class={joinClass('font-ns pt-[5px] px-[10px] flex flex-col  w-full justify-between', this.isDisplayOnly ? '' : 'h-[180px]')}>
        <div class="flex flex-col gap-[.35rem]">
          {/* <span class="block text-[16px] font-medium">{this.product.name.sv}</span> */}
          <span class="block text-[16px] font-medium">{widgetState.language === 'en' && this.product?.name?.en ? this.product?.name?.en : this.product?.name?.sv}</span>

          {/* Brand name and variant name and color name */}
          <span class=" text-[10px] font-light text-[#5E5E5E]">{`${
            widgetState.language === 'en' && this.product?.brandName?.en
              ? joinArray([this.product?.brandName?.en, this.product?.variantName?.en])
              : joinArray([this.product?.brandName?.sv, this.product?.variantName?.sv])
          }`}</span>
          {!this.isDisplayOnly && (
            <div class="text-[10px] font-normal text-[#5E5E5E] overflow-scroll h-[70px]">
              {widgetState.language === 'en' ? this.product?.description?.en : this.product?.description?.sv}
            </div>
          )}
        </div>
        <div class="flex justify-between pt-[5px] w-full">
          <span class="font-normal text-[14px]">
            {this.product?.price?.value} <span class=" text-[#727272] font-light text-[12px]">{i18next.t('productCard.excludedVat')}</span>
          </span>
          {!this.isDisplayOnly && (
            <svg xmlns="http://www.w3.org/2000/svg" class={joinClass('w-5 h-5 ', this.selected ? 'text-black' : 'text-gray-300')} viewBox="0 0 20 20" fill="currentColor">
              <path
                fill-rule="evenodd"
                d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z"
                clip-rule="evenodd"
              />
            </svg>
          )}
        </div>
      </div>
    );
    return (
      <Host>
        <div
          class={joinClass(
            ' min-h-[100px] h-full rounded-xl flex flex-col place-items-center  border-[1px] cursor-pointer bg-white overflow-hidden',
            this.selected && !this.isDisplayOnly ? 'border-[#000] ' : 'hover:border-[#a7a7a7] transition duration-100 ease-out hover:ease-in',
            this.isDisplayOnly ? 'w-[160px] py-[8px]' : 'w-[240px] py-[10px]',
          )}
        >
          {picture}
          {content}
        </div>
      </Host>
    );
  }
}
